<script lang="ts">
	import type { ChartRect, JoinedRace, ScatterCoords } from "+/modules/types";
	import createSeasonChart from "+/modules/charting/createSeasonChart";
	import updateSeasonChart from "+/modules/charting/updateSeasonChart";
	import { getCoords, mouseClickHandler, mouseMoveHandler } from "+/modules/charting/seasonChartInteraction";
	import SeasonChartTooltip from "+/components/SeasonChartTooltip.svelte";

	export let races: JoinedRace[],
		focusedRace: JoinedRace = undefined;

	const chartRect: ChartRect = {
			width: 768,
			height: 32,
			top: 0,
			left: 0,
			right: 0,
			bottom: 32,
			gap: 16
		},
		scatterRadius = 4,
		scatterY = scatterRadius + 8;

	let chartElement: HTMLElement,
		chart: d3.Selection<Element, unknown, null, undefined>,
		xScale: d3.ScaleTime<number, number, never>,
		hoverTarget: JoinedRace,
		hoverCoords: ScatterCoords,
		focusedCoords: ScatterCoords;

	function tooltipMouseHandler() {
		hoverTarget = null;
	}

	// Create chart
	$: chart = createSeasonChart(chartElement, chartRect);

	// Add event listeners
	$: if (chart) chart.select("svg")
		.on("click", event => focusedRace = mouseClickHandler(event, races, focusedRace, xScale, scatterY))
		.on("mousemove", event => hoverTarget = mouseMoveHandler(event, races, hoverTarget, xScale, scatterY));

	// Update chart
	$: xScale = updateSeasonChart(chart, chartRect, scatterRadius, scatterY, races);

	// Set tooltip once chart is made
	$: focusedCoords = getCoords(chartElement, focusedRace, xScale, scatterY, scatterRadius);
	$: hoverCoords = getCoords(chartElement, hoverTarget, xScale, scatterY, scatterRadius);
</script>

<style type="text/scss">
	#season-chart {
		margin:48px 0;

		:global(.xaxis) {
			font-size: 14px;
			color: black;

			:global(g:last-child) {
				visibility:hidden;
			}
		}

		:global(.scatter) {
			fill:#343434;
		}

		&.interact {
			cursor:pointer;
		}
	}
</style>

<SeasonChartTooltip race={(hoverTarget || focusedRace)} coords={hoverCoords || focusedCoords} on:mouseleave={tooltipMouseHandler} />
<div id="season-chart" class:interact={hoverTarget} bind:this={chartElement}></div>